import { useState } from "react";
import { Link } from "react-router-dom";
import Helmet from "react-helmet";
// import Headroom from "react-headroom";
import logo from "../../assets/images/header/logo.svg";
import arrow from "../../assets/images/header/arrowicon.svg";
// import menu from "../../assets/images/header/menu.svg";
import "./header.css";

const WhatNavList = [
  {
    name: "whatNav",
    nav: "Immigrate",
    url: "/",
    subNav: [
      {
        subTitle: "Express Entry",
        url: "/",
        finalTitle: [
          "A fast-track immigration program for permanent residency.",
        ],
      },
      {
        subTitle: "Provincial Nominee Programs",
        url: "/",
        finalTitle: [],
      },
      {
        subTitle: "Atlantic Immigration Program",
        url: "/",
        finalTitle: [],
      },
    ],
  },
  {
    name: "whatNav",
    nav: "Work & Travel",
    url: "/",
    subNav: [],
  },
  {
    name: "whatNav",
    nav: "Study",
    url: "/",
    subNav: [],
  },
  {
    name: "whatNav",
    nav: "Business",
    url: "/",
    subNav: [
      {
        subTitle: "Business Item",
        url: "/",
        finalTitle: [
          "A fast-track immigration program for permanent residency.",
        ],
      },
      {
        subTitle: "Business Item",
        url: "/",
        finalTitle: [],
      },
      {
        subTitle: "Business Item",
        url: "/",
        finalTitle: [],
      },
    ],
  },
  {
    name: "whatNav",
    nav: "Sponsorship",
    url: "/",
    subNav: [
      {
        subTitle: "Sponsorship Item",
        url: "/",
        finalTitle: [
          "A fast-track immigration program for permanent residency.",
        ],
      },
      {
        subTitle: "Sponsorship Item",
        url: "/",
        finalTitle: [],
      },
      {
        subTitle: "Sponsorship Item",
        url: "/",
        finalTitle: [],
      },
    ],
  },
];

const MobNavList = [
  {
    nav: "What",
    name: "whatMobNav",
    subNav: [
      {
        title: "Immigrate",
        url: "/",
      },
      {
        title: "Work & Travel",
        url: "/",
      },
      {
        title: "Study",
        url: "/",
      },
      {
        title: "Business",
        url: "/",
      },
      {
        title: "Sponsorship",
        url: "/",
      },
    ],
  },
  {
    nav: "Why",
    name: "whyMobNav",
    subNav: [
      {
        title: "Reviews",
        url: "/",
      },
      {
        title: "RCIC",
        url: "/",
      },
    ],
  },
  {
    nav: "How",
    name: "howMobNav",
    subNav: [
      {
        title: "Education center",
        url: "/",
      },
      {
        title: "FAQ’s",
        url: "/",
      },
    ],
  },
];

// console.log("WhatNavList>>", WhatNavList);
const Header = () => {
  const [iconView, setIconView] = useState(false);
  const [hoverMenu, setHoverMenu] = useState("");
  const [mobMenu, setMobMenu] = useState("");
  const [mobMenuSubNav, setMobMenuSubNav] = useState([]);
  const [subNav, setSubNav] = useState();
  const [active, setActive] = useState("");

  const handleMenuIcon = () => {
    setIconView(!iconView);
    setMobMenu("");
  };

  const handleEvent = (a) => {
    if (hoverMenu === "whatNav") {
      setHoverMenu("");
    } else if (hoverMenu === "whyNav") {
      setHoverMenu("");
    } else if (hoverMenu === "howNav") {
      setHoverMenu("");
    }
    setHoverMenu(a);
    setActive("");
    setSubNav();
  };

  const handleMobMenu = (name, subNavItems) => {
    setMobMenu(name);
    setMobMenuSubNav(subNavItems);
  };

  const handleMenuClick = (obj) => {
    setSubNav(obj);
    setActive(obj?.nav);
  };

  return (
    // <Headroom>
    <>
      <Helmet>
        <html
          className={`${hoverMenu === "whatNav" ? "bg-shadow" : ""}`}
          lang="en"
        />
      </Helmet>
      <div className="header">
        <div className="container">
          <div className="header-content">
            <div className="logo">
              <Link to="/">
                <img src={logo} alt="logo" />
              </Link>
            </div>
            <div className="header-menu">
              <div className="mega-menu" onClick={() => handleEvent("whatNav")}>
                <div className="header-menu-list">
                  <span>What</span>
                  <img
                    style={{
                      rotate: `${hoverMenu === "whatNav" ? "180deg" : "0deg"}`,
                      transition: "all 0.3s",
                    }}
                    src={arrow}
                    alt="arrow"
                  />
                </div>

                {hoverMenu === "whatNav" && (
                  <div
                    className="menu-list-wrapper what-nav-wrapper"
                    onMouseLeave={() => handleEvent("")}
                  >
                    <div className="container">
                      <ul className="menu-list-container">
                        {WhatNavList.map((e, i) => (
                          <li className={`main-nav-list`} key={i}>
                            <span
                              className={`menu-list-item ${ 
                                active === e?.nav ? "active" : ""
                              } ${e?.nav}`}
                              onMouseOver={() => handleMenuClick(e)}
                              id={e?.nav}
                            >
                              {e?.nav}
                            </span>
                          </li>
                        ))}
                        <div className="menu-list-category">
                          {subNav?.subNav.map((el) => (
                            <li>
                              <span
                                className={`menu-list-item ${el?.subTitle}`}
                                onClick={(e) =>
                                  handleMenuClick(e.target.id, el?.url)
                                }
                                id={el?.subTitle}
                              >
                                {el?.subTitle}
                              </span>
                              <ul className="menu-list-subcategory">
                                {el?.finalTitle.map((elem) => (
                                  <li>
                                    <span className="menu-list-item">
                                      {elem}
                                    </span>
                                  </li>
                                ))}
                              </ul>
                            </li>
                          ))}
                        </div>
                        {/* <li>
                          <Link className="menu-list-item" to="/">
                            Work & Travel
                          </Link>
                        </li>
                        <li>
                          <Link className="menu-list-item" to="/">
                            Study
                          </Link>
                        </li>
                        <li>
                          <Link className="menu-list-item" to="/">
                            Business
                          </Link>
                        </li>
                        <li>
                          <Link className="menu-list-item" to="/">
                            Sponsorship
                          </Link>
                        </li> */}
                      </ul>
                    </div>
                  </div>
                )}
              </div>
              <div className="mega-menu" onClick={() => handleEvent("whyNav")}>
                <div className="header-menu-list">
                  <span>Why</span>
                  <img
                    style={{
                      rotate: `${hoverMenu === "whyNav" ? "180deg" : "0deg"}`,
                      transition: "all 0.3s",
                    }}
                    src={arrow}
                    alt="arrow"
                  />
                </div>

                {hoverMenu === "whyNav" && (
                  <div
                    className="menu-list-wrapper  why-nav-wrapper"
                    onMouseLeave={() => handleEvent("")}
                  >
                    <div className="container">
                      <ul className="menu-list-container">
                        <li>
                          <Link className="menu-list-item" to="/">
                            Reviews
                          </Link>
                        </li>
                        <li>
                          <Link className="menu-list-item" to="/rcic">
                            RCIC
                          </Link>
                        </li>
                      </ul>
                    </div>
                  </div>
                )}
              </div>
              <div className="mega-menu" onClick={() => handleEvent("howNav")}>
                <div className="header-menu-list">
                  <span>How</span>
                  <img
                    style={{
                      rotate: `${hoverMenu === "howNav" ? "180deg" : "0deg"}`,
                      transition: "all 0.3s",
                    }}
                    src={arrow}
                    alt="arrow"
                  />
                </div>

                {hoverMenu === "howNav" && (
                  <div
                    className="menu-list-wrapper how-nav-wrapper"
                    onMouseLeave={() => handleEvent("")}
                  >
                    <div className="container">
                      <ul className="menu-list-container">
                        <li>
                          <Link className="menu-list-item" to="/">
                            Education center
                          </Link>
                        </li>
                        <li>
                          <Link className="menu-list-item" to="/faq">
                            FAQ’s
                          </Link>
                        </li>
                        <li>
                          <Link className="menu-list-item" to="/">
                            Free assessment
                          </Link>
                        </li>
                      </ul>
                    </div>
                  </div>
                )}
              </div>
            </div>
            <div
              className="toggle-icon menu-toggle-wrapper"
              onClick={handleMenuIcon}
              role="presentation"
            >
              {iconView ? <CloseIcon /> : <MenuIcon />}
            </div>
          </div>
        </div>
        {iconView && (
          <div className="menu-list-wrapper">
            <div className="container">
              {mobMenu === "" ? (
                <div className="sub-menu-wrapper">
                  <div className="submenu-mob">
                    {MobNavList.map((elem, i) => (
                      <div className="mob-mega-menu" key={i}>
                        <div className="header-menu-list">
                          <span
                            onClick={() =>
                              handleMobMenu(elem?.name, elem.subNav)
                            }
                          >
                            {elem?.nav}
                          </span>
                          <DropIcon />
                        </div>
                      </div>
                    ))}
                  </div>
                  <ul className="menu-list-container">
                    <li>
                      <Link className="menu-list-item" to="/about">
                        About
                      </Link>
                    </li>
                    <li>
                      <Link className="menu-list-item" to="/">
                        Blog
                      </Link>
                    </li>
                    <li>
                      <Link className="menu-list-item" to="/contact">
                        Contact Us
                      </Link>
                    </li>
                    <li>
                      <span className="menu-list-item">
                        Personal Portal / Registration
                      </span>
                    </li>
                    <li>
                      <span className="menu-list-item">Search</span>
                      <span className="menu-list-item">En/Fr</span>
                    </li>
                  </ul>
                  <div className="menu-cta-btn">
                    <Link className="cta-btn" to="/">
                      Free Assessment
                    </Link>
                  </div>
                </div>
              ) : (
                <div className="sub-menu-mob-list">
                  <div className="submenu-mob">
                    {mobMenuSubNav.length > 0
                      ? mobMenuSubNav.map((el) => (
                          <Link className="menu-list-item" to={el?.url}>
                            {el?.title}
                          </Link>
                        ))
                      : ""}
                  </div>
                </div>
              )}
            </div>
          </div>
        )}
      </div>
    </>
    // </Headroom>
  );
};

export default Header;

const MenuIcon = () => {
  return (
    <svg
      width="34"
      height="27"
      viewBox="0 0 34 27"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <line x1="6" y1="8" x2="28" y2="8" stroke="#252D43" strokeWidth="2" />
      <line x1="6" y1="13" x2="28" y2="13" stroke="#252D43" strokeWidth="2" />
      <line x1="6" y1="18" x2="28" y2="18" stroke="#252D43" strokeWidth="2" />
    </svg>
  );
};

const CloseIcon = () => {
  return (
    <svg
      width="34"
      height="27"
      viewBox="0 0 34 27"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M24 7.41L22.59 6L17 11.59L11.41 6L10 7.41L15.59 13L10 18.59L11.41 20L17 14.41L22.59 20L24 18.59L18.41 13L24 7.41Z"
        fill="black"
      />
    </svg>
  );
};

const DropIcon = () => {
  return (
    <svg
      width="12"
      height="8"
      viewBox="0 0 12 8"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M10.59 0.294983L6 4.87498L1.41 0.294983L0 1.70498L6 7.70498L12 1.70498L10.59 0.294983Z"
        fill="white"
      />
    </svg>
  );
};
